@use "global" as *;

//人気記事
.article {
  max-width: rem(294);
  @include mq("md") {
    margin-left: rem(80);
  }
}

.article__content {
  margin-top: rem(83);
  min-width: rem(294);
}

.article__content:first-child {
  @include mq("md") {
  margin-top: rem(0);
  }
}

.article-cards__card:not(:first-child) {
  margin-top: rem(24);
}

//口コミ
.article__reviews {
  margin-top: rem(20);
}

.article__reviews img {
  aspect-ratio: 294 / 218;
}

.article__category {
  margin-top: rem(16);
  font-size: rem(12);
  color: $blue;
}

.article__sub-text {
  margin-top: rem(8);
  font-size: rem(24);
  color: $blue;
}

.article__button {
  margin-top: rem(40);
  text-align: center;
}


