.article__archive {
  margin-top: rem(30);
}

.article__date,
.article__month {
  font-size: rem(24);
  color: $green;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.article__date img {
  width: rem(24);
  margin-right: rem(6);
  transition: transform 0.3s ease; /* アニメーションを追加 */
}

.article__date-wrapper {
  margin-bottom: rem(10);
}

.article__months {
  display: none; /* 初期状態で隠す */
  overflow: hidden;
  padding: 0;  /* 初期状態でのpaddingを0に */
  margin: 0;   /* 初期状態でのmarginを0に */
}

.article__month {
  margin-left: rem(30);
}

.article__month img {
  width: rem(20);
  margin-right: rem(6);
}
