@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////

$base-font-family: "Noto Sans JP", sans-serif;
$second-font-family: "Noto Serif JP", serif;
$eng-font-family: "Gotu", sans-serif;
$eng-second-font-family: "Lato", sans-serif;

// ※検証ツールでしっかり反映されているか確認する

body {
  font-family: $base-font-family;
}

// フォントウェイト
$light: 300;
$regular: 400;
$normal: 500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$white: #FFFFFF;
$green: #408F95;
$skyblue: #DDF0F1;
$blue: #0D2936;
$gray: #D9D9D9;
$brown: #796A64;
$green2: #1C595E;
$red: #C94800;
