.accordion__title {
  position: relative;
  font-size: 1rem;
  padding: 1rem;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  cursor: pointer;
  color: #FFFFFF;
  background-color: #408F95;
}
@media screen and (min-width: 768px) {
  .accordion__title {
    padding: 1.5625rem 2.5rem;
    font-size: 1.25rem;
  }
}

.accordion__title::after,
.accordion__title::before {
  position: absolute;
  content: "";
  display: block;
  width: 2.0625rem;
  height: 0.0625rem;
  background-color: #FFFFFF;
  right: 11px;
  top: 50%;
  transform: translate(0, -50%);
  transition: 0.3s transform, 0.3s opacity;
}
@media screen and (min-width: 768px) {
  .accordion__title::after,
  .accordion__title::before {
    right: 40px;
  }
}

.accordion__title.is-open::after {
  content: "";
  position: absolute;
  background-color: #fff;
  width: 2.0625rem;
  height: 0.0625rem;
  right: 11px;
  top: 50%;
  transform: translate(0, -50%);
}
@media screen and (min-width: 768px) {
  .accordion__title.is-open::after {
    right: 40px;
  }
}

/* 初期状態: 「-」アイコン */
.accordion__title::before {
  width: 2.0625rem;
  height: 0.0625rem;
  background-color: #FFFFFF;
  right: 11px;
  top: 50%;
  transform: translate(0, -50%) rotate(0deg); /* 垂直線 */
}

.accordion__title::after {
  width: 2.0625rem;
  height: 0.0625rem;
  background-color: #FFFFFF;
  right: 11px;
  top: 50%;
  transform: translate(0, -50%) rotate(0deg); /* 水平線 */
}

/* アコーディオンが開いたときのスタイル */
.accordion__title.is-open::before {
  transform: translate(0, -50%) rotate(180deg); /* 水平線 */
}

.accordion__title.is-open::after {
  transform: translate(0, -50%) rotate(90deg); /* 垂直線 */
}

