/* WP-PageNavi全体のスタイル */
.wp-pagenavi {
  display: flex;
  justify-content: center;
  gap: 0.625rem; /* 初期の全体間隔 */
  list-style: none;
  padding: 0;
  margin: 2.4375rem 0;
}

/* 数字間の余白 */
.wp-pagenavi a,
.wp-pagenavi span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.375rem;
  height: 2.375rem;
  font-size: 0.875rem;
  text-decoration: none;
  border: 1px solid #408F95;
  color: #408F95;
  transition: background-color 0.3s ease, color 0.3s ease;
  margin: 0 0.3125rem; /* 数字間の余白を設定 */
}

/* 「前へ」「次へ」のリンク用スタイル */
.wp-pagenavi .previouspostslink,
.wp-pagenavi .nextpostslink {
  width: auto; /* 幅をコンテンツに応じて調整 */
  padding: 0 0.9375rem; /* 内部余白を設定 */
  border: 1px solid #408F95;
  color: #408F95;
  margin: 0 1.6875rem; /* 「前へ」「次へ」と数字の間を広げる */
}

/* ホバー時のスタイル */
.wp-pagenavi a:hover {
  background-color: #408F95;
  color: #FFFFFF;
}

/* 現在のページ番号のスタイル */
.wp-pagenavi .current {
  background-color: #408F95;
  color: #FFFFFF;
}

/* 前後リンクの矢印画像スタイル */
.wp-pagenavi .previouspostslink img,
.wp-pagenavi .nextpostslink img {
  width: 1rem; /* 矢印の幅 */
  height: 1rem; /* 矢印の高さ */
  display: inline-block;
  vertical-align: middle;
  transition: filter 0.3s ease;
}

/* 前後リンクの矢印画像のホバー時スタイル */
.wp-pagenavi .previouspostslink:hover img,
.wp-pagenavi .nextpostslink:hover img {
  filter: brightness(0) invert(1) brightness(1.2); /* ホバー時に白色化 */
}

