.tab__content {
  display: none;
  padding: 1.0625rem 1.25rem 1.25rem 1.25rem;
  border: 0.0625rem solid #408F95;
}

.tab__content img {
  aspect-ratio: 297/189;
}
@media screen and (min-width: 768px) {
  .tab__content img {
    width: 48%;
  }
}

@media screen and (min-width: 768px) {
  .tab__item {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.tab__content.is-active {
  display: block;
}

.tab__wrap {
  width: 100%;
}
@media screen and (min-width: 768px) {
  .tab__wrap {
    margin-right: 2.5rem;
  }
}

.tab__title {
  font-size: 1.5rem;
  font-weight: 700;
  border-bottom: 0.0625rem solid #408F95;
  width: 100%;
  display: inline-block;
}

.tab__text {
  font-size: 1rem;
  font-weight: 400;
  margin-top: 0.5rem;
  letter-spacing: 0.0125rem;
}
@media screen and (min-width: 768px) {
  .tab__text {
    letter-spacing: 0.03125rem;
  }
}

.tab__img {
  margin-top: 0.625rem;
}
@media screen and (min-width: 768px) {
  .tab__img {
    margin-top: 0rem;
  }
}

.thanks {
  padding-bottom: 3.0625rem;
}
@media screen and (min-width: 768px) {
  .thanks {
    padding-bottom: 0;
  }
}

.thanks__inner {
  position: relative;
}

.thanks__inner::before {
  content: "";
  position: absolute;
  background-image: url(../images/common/gallery-icon.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 6.8125rem;
  height: 3.125rem;
  transform: scaleX(-1);
  left: calc(50% - min(48.5vw, 41.875rem));
  left: -50;
  top: 1%;
}
@media screen and (min-width: 768px) {
  .thanks__inner::before {
    width: 18.8125rem;
    height: 8.625rem;
    left: calc(50% - min(48.5vw, 41.875rem));
    left: -50;
    top: -9%;
  }
}

.thanks__text-top,
.thanks__text-bottom {
  text-align: center;
  font-size: 1rem;
  font-weight: 500;
  line-height: normal;
}

.thanks__text-top {
  line-height: 1.125;
}

.thanks__text-bottom {
  margin-top: 2.5rem;
}

.title {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.title__main {
  font-family: "Gotu", sans-serif;
  font-size: 2rem;
  font-weight: 400;
  color: #0D2936;
}
@media screen and (min-width: 768px) {
  .title__main {
    font-size: 2.5rem;
  }
}

.title__main::first-letter {
  color: #408F95;
}

.title__sub {
  font-size: 0.75rem;
  font-weight: 400;
  color: #408F95;
}

.title__main--blog {
  font-family: "Gotu", sans-serif;
  font-size: 2rem;
  font-weight: 400;
  color: #0D2936;
}
@media screen and (min-width: 768px) {
  .title__main--blog {
    font-size: 2.5rem;
    color: #FFFFFF;
  }
}

.title__sub--blog {
  font-size: 0.75rem;
  font-weight: 400;
  color: #408F95;
}
@media screen and (min-width: 768px) {
  .title__sub--blog {
    color: #FFFFFF;
  }
}

.title__main--blog::first-letter {
  color: #408F95;
}
@media screen and (min-width: 768px) {
  .title__main--blog::first-letter {
    color: #FFFFFF;
  }
}

.title__main--contact-top.title__main {
  line-height: 1;
  font-size: 5rem;
  font-weight: 400;
  font-family: "Gotu", sans-serif;
}

.title__sub--contact-top.title__sub {
  line-height: 1;
}

.voice-card {
  display: flex;
  flex-direction: column;
  height: 100%;
  transition: all 0.3s ease 0s;
  width: 100%;
  background-color: #FFFFFF;
  padding: 0rem 1rem 1.5rem 1rem;
  box-shadow: 0.125rem 0.125rem 0.625rem rgba(0, 0, 0, 0.25);
}
@media screen and (min-width: 768px) {
  .voice-card {
    width: 100%;
    padding: 1.5rem;
  }
}

.voice-card:hover {
  opacity: 0.6;
}

.voice-card__box {
  margin-top: 0.9375rem;
  display: flex;
}
@media screen and (min-width: 768px) {
  .voice-card__box {
    margin-top: 0rem;
    display: flex;
    justify-content: space-between;
  }
}

.voice-card__item-information {
  font-size: 0.75rem;
  font-weight: 400;
}

.voice-card__item-tag {
  color: #408F95;
  font-size: 0.75rem;
  line-height: 1;
  font-weight: 400;
  border: 1px solid #408F95;
  padding: 0.5rem 0.625rem;
  display: inline-block;
  text-align: left;
}
@media screen and (min-width: 768px) {
  .voice-card__item-tag {
    padding: 0.5625rem 0.9375rem;
    margin-left: auto;
  }
}

.voice-card__items {
  flex-grow: 1;
  margin-top: 1.5rem;
  margin-right: 0.8125rem;
}
@media screen and (min-width: 768px) {
  .voice-card__items {
    max-width: none;
  }
}

.voice-card__wrap {
  display: block;
  text-align: left;
  margin-top: -1.3125rem;
}
@media screen and (min-width: 768px) {
  .voice-card__wrap {
    display: flex;
    align-items: flex-end;
    margin-top: -0.375rem;
  }
}

.voice-card__item-title {
  font-size: 1rem;
  font-weight: 500;
  color: #0D2936;
  border-bottom: 1px solid #408F95;
  margin-top: 0.1875rem;
  text-align: left;
  word-break: break-word;
}
@media screen and (min-width: 768px) {
  .voice-card__item-title {
    font-size: 1.5rem;
    font-weight: 500;
    margin-top: 0.5625rem;
  }
}

.voice-card__item-img {
  flex-shrink: 0;
  width: 9.4375rem;
  height: auto;
}
@media screen and (min-width: 768px) {
  .voice-card__item-img {
    width: 11.25rem;
  }
}

.voice-card__item-img img {
  width: 100%;
  aspect-ratio: 151/117;
  object-fit: cover;
}
@media screen and (min-width: 768px) {
  .voice-card__item-img img {
    aspect-ratio: 180/140;
  }
}

.voice-card__item-text {
  font-size: 1rem;
  font-weight: 400;
  color: #0D2936;
  font-family: "Noto Sans JP", sans-serif;
  margin-top: 0.9375rem;
  text-align: left;
}
@media screen and (min-width: 768px) {
  .voice-card__item-text {
    margin-top: 2.4375rem;
  }
}

.voice-cards {
  display: grid;
  gap: 2.4375rem;
  grid-template-columns: repeat(1, 1fr);
}
@media screen and (min-width: 768px) {
  .voice-cards {
    row-gap: 5rem;
    column-gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.voice__inner {
  max-width: 37.5rem;
}
@media screen and (min-width: 768px) {
  .voice__inner {
    position: relative;
  }
}

@media screen and (min-width: 768px) {
  .voice__inner::before {
    content: "";
    position: absolute;
    width: 4.4375rem;
    height: 10.125rem;
    top: 34.375rem;
    right: calc(50% - min(48.5vw, 33.75rem));
    background-image: url(../images/common/Voice-icon2.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    z-index: 100;
  }
}

@media screen and (min-width: 768px) {
  .voice__inner::after {
    content: "";
    position: absolute;
    width: 18.8125rem;
    height: 8.625rem;
    top: -6.5625rem;
    left: calc(50% - min(48.5vw, 38.75rem));
    background-image: url(../images/common/Voice-icon1.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    z-index: 100;
  }
}

.voice__cards {
  margin-top: 3.125rem;
}
@media screen and (min-width: 768px) {
  .voice__cards {
    margin-top: 2.5rem;
  }
}

.voice__button {
  text-align: center;
  margin-top: 2.5rem;
}
@media screen and (min-width: 768px) {
  .voice__button {
    margin-top: 5rem;
  }
}